.banner {
  padding: 12px 0 6px;
  .banner__logo {
    .logo {

    }
  }
  .banner__title {
    span {
      font-size: 28px;
    }
  }
  .banner__button {
    .nav-control {
      display: none;
    }
    .button.join {
      display: inline-block;
      padding: 8px 16px 10px;
      font-size: 22px;
      transition: all 0.2s ease;
      span {
        display: inline-block;
      }
      &:hover, &:focus {
        transform: scale(1.1);
        background-color: darken(@scout-green, 5%);
      }
    }
  }
}
// Main content area
.main {
   .page__featured-image {
    height: 300px;
    align-items: baseline;
    // border-bottom: none;

  }
  .page__wrap {
    &.page__image {
      // margin-top: -120px;
      // border-top: 3px solid @scout-green;
      background-color: white;

      .page__title {
        padding-top: 0;
        h1 {
          margin:30px 0 0;
        }
      }
    }
  }
  .page__columns {
    .make-column(10);
    .make-column-offset(1);
  }
}
.breadcrumbs-search {
    .search {
        .search-field {
            transition: all 2s ease;
            &:active,
            &:focus {
                width: 400px;
            }
        }
    }
}
.content-info {
  a {
    transition: all 0.3s ease;
    border-bottom: 1px solid white;
    &:hover,
    &:focus {
      background-color: white;
      color: @brand-primary;
    }
  }
}
