.page {}

.main .page__wrap.page__image {
    .page__builder {
        .page__wrap {
            padding: 0 !important;
            width: 100% !important;
        }
    }
}

.page__wrap {}

.page__builder {
    .page__builder__container {
        padding: 30px 0px;
        >.inner {
            display: flex;
        }
    }
    h1,
    h2,
    h3,
    h4,
    {
        text-indent: 2px;
        margin: 15px 0px 5px 0px;
    }
    h2 {
        font-size: 24px;
        margin-top: 0px;
    }
    h3 {
        font-size: 20px;
    }
    .one_column {
        .wysiwyg_editor {
            .make-column(12,0);
        }
    }
    .two_column {
        .wysiwyg_editor {
            .make-column(12);
        }
        &.minibus {
            #sbc-calendar .sbc-calendar-month {
                border-radius: 4px;
            }
        }
    }
    .three_column {
        .wysiwyg_editor {
            .make-column(12);
        }
    }
    .four_column {
        .wysiwyg_editor {
            .make-column(12);
        }
    }
    .one_column,
    .two_column,
    .third_two_thirds,
    .two_thirds_thirds,
    .three_column,
    .four_column {
        .wysiwyg_editor {
            img {
                .img-responsive(@display: inline-block);
            }
        }
    }
    .leaders {
        // padding: 0 10px;
        h2 {
            margin-bottom: 20px;
        }
        .leader+.leader {}
        .leader {
            text-align: center;
            padding: 10px 0;
            img {
                width: 150px;
                height: 150px;
            }
            background-color: #fafafa;
            a {
                word-break: break-all;
            }
        }
    }
    .leaders+.leaders {
        margin: 30px 0;
    }
    .leader__details {
        padding-top: 10px;
    }
    .call_to_action {
        clear: both;
        padding: 20px 0px 10px 0px;
        a {
            //.button-variant(transparent, 1px solid white);
            float: right;
            margin-top: 5px;
            &:hover {
                background: #fff;
                color: @brand-primary;
            }
        }
    }
    iframe {
        max-width: 100%;
        max-height: 300px;
    }
    .schedule {
        .schedule__inner {
            .make-column(12);
            h2 {}
            table {
                width: 100%;
                text-align: left;
                thead {
                    background: fade(@black, 50%);
                    color: white;
                }
                th,
                td {
                    padding: 8px;
                }
                tr:nth-child(even) {
                    background: @grey-lighter;
                }
            }
        }
    }
    .tablepress tfoot th,
    .tablepress thead th {
        background: fade(@black, 50%);
        color: white;
    }
}

.bg-cols {
    padding-top: 0px !important; // sorry
    .inner {
        position: relative;
        padding: 10px 20px 80px 20px;
        background: #f3f3f3;
        margin-bottom: 20px;
        h3 {
            font-size: 18px;
        }
        a.button {
            position: absolute;
            bottom: 20px;
            left: 20px;
            right: 20px;
        }
    }
}

.newsletter {
    background: @brand-primary;
    padding-top: 0px !important; // sorry
    .gform_footer {
        text-align: center;
        clear: none;
        padding: 0;
        margin: 2px 0 0 0;
        .gform_button {
            //.button-variant(transparent, 1px solid #fff);
            &:hover {
                background: #fff;
                color: black;
            }
        }
    }
}

.quote-cols {
    padding-bottom: 40px !important;
    .inner {
        border-bottom: 5px solid @brand-primary;
        padding: 20px 20px 20px 20px;
        font-size: 18px;
        background: #f4f4f4;
        min-height: 210px;
        blockquote {
            p {
                text-align: left;
            }
        }
    }
    p {
        text-align: center;
    }
    .gform_wrapper {
        li.gfield {
            width: 100%;
        }
    }
}

.accordion__wrapper {
    .clearfix();
}

.page-slideshow {
    position: relative;
    
    .page-slideshow__inner {
        .make-column(12);
        a.button-green {
            position: absolute;
            z-index: @zindex-home-button;
            top: 85%;
            left: calc(~'50% - 60px');
            font-weight: bold;
            transition: all 0.2s ease;
            &:hover, &:focus {
                transform: scale(1.1);
            }
        }
    }
    .page-slideshow__slick {
        // border-left: 1px solid transparent;
        // border-right: 1px solid transparent;
        
    }
    .page-slideshow__item {
        .bg-cover(480px);
        position: relative;
        p {
            margin-bottom: 0;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            background-color: white;
            background-color: rgba(255, 255, 255, 0.8);
            padding: 20px;
        }
    }
}

.page-gallery {
    .page-gallery__inner {}
    .page-gallery__item {
        .make-column(6);
        padding: 10px;
    }
    .page-gallery__image {
        min-height: 150px;
        .bg-cover(150px);
    }
}

.section-logos {
    padding-top: 0 !important;
    >p {
        padding: 0 10px;
    }
    .section-logos__header {
        margin: 0 auto;
        max-width: @container-desktop;
    }
    .section-logos__container {
        display: flex;
        // flex-direction: column;
        text-align: center;
        margin: 30px auto 0;
        max-width: @container-large-desktop;
        flex-wrap: wrap;
    }
    .section {
        padding: 10px;
        flex-basis: 50%;
        display: flex;
        align-content: center;
        justify-content: center;
        flex-direction: column;
        a {
            &:hover, &:focus, &:active {
                background-color: transparent;
            }
        }
        img {
            .img-responsive(@display: inline-block);
        }
        sup {
            top: -0.2em;
            margin-left: -0.2em;
        }
        p {
            margin-top: auto;
        }
    }
}